import { useState } from "react";
import { Row, Col } from "antd";
import { withTranslation } from "react-i18next";
import { Slide } from "react-awesome-reveal";
import { Button } from "../../common/Button";
import { MissionBlockSection, Content, ContentWrapper } from "./styles";

interface MissionBlockProps {
  title: string;
  content: string;
  button: string;
  t: any;
  id: string;
}

const MissionBlock = ({ title, content, button, t, id }: MissionBlockProps) => {
  const [visible, setVisibility] = useState(false);
  const scrollTo = (id: string) => {
    const element = document.getElementById(id) as HTMLDivElement;
    element.scrollIntoView({
      behavior: "smooth",
    });
    setVisibility(false);
  };
  return (
    <MissionBlockSection>
      <Slide direction="up">
        <Row justify="center" align="middle" id={id}>
          <ContentWrapper>
            <Col lg={24} md={24} sm={24} xs={24}>
              <h6>{t(title)}</h6>
              <Content>{t(content)}</Content>
              {button && (
                <Button name="submit" onClick={() => scrollTo("games")}>
                  {t(button)}
                </Button>
              )}
            </Col>
          </ContentWrapper>
        </Row>
      </Slide>
    </MissionBlockSection>
  );
};

export default withTranslation()(MissionBlock);
